<template>
	<div id="userCenter">
		<!--  -->
		<Loading v-if="pageLoading"></Loading>
		<!--  -->
		<div v-else>
			<!--  -->
			<div class="cont_frame user_info_list">
				{{ userAccount.userName }}
			</div>
			<ul>
				<li class="cont_frame user_info_list" @click="gotoChangePwd">
					<i class="list_icon icon_password"></i><i class="icon-next"></i>
				</li>
				<li class="cont_frame user_info_list" @click="logOut">
					<i class="list_icon icon_exit"></i><i class="icon-next"></i>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	// 
	import Loading from "components/Common/Loading.vue";
	// js
	import Common from 'common/common.js';
	import { GetCookie } from 'common/important.js';
	import { ClearAccount } from 'common/account.js';
	// Api
	import Api from "api/user_center.js";
	//Vuex
	import { mapGetters } from 'vuex';

	export default {
		name: "userCenter",
		components: { Loading },
		computed: {
            ...mapGetters([ 'userAccount' ]),
        },
		data(){
			return{
				// 
				isLogined: false,
				// 
				pageLoading: false
			}
		},
		created(){
			this.init();
		},
		methods:{
			init(){
                this.$store.commit('SET_NAV_TITLE', '');
			},
			// 
			gotoChangePwd(){
				Common.GotoPage('ChangePassword', {}, this);
			},
			// 
			logOut(){
				ClearAccount(this.$store.commit);
				Common.GotoPage('UnLogined', {}, this);
			}
		}
	};
</script>

<style lang="less" scoped>
	// 
	@import "../../assets/less/setting";

	/* user_info_list */

	.user_info_list{
		cursor: pointer;

		.list_icon{			
			color: @ic_gray_color;

			.mr(5);
		}

		.icon-next{
			color: #ddd;
			float: right;

			.mt(5);
			.ft(12);
		}
	}
</style>



