<template>
	<div id="newsDetail">
        <!--  -->
		<Loading v-if="pageLoading"></Loading>
		<!--  -->
		<div v-else>
            <div class="cont_frame">
                <!--  -->
                <h2 class="article_title">{{ newsCont.title }}</h2>
                <!--  -->
                <section class="article_time">{{ newsCont.publishDate }}</section>
                <section class="article_tags">{{ newsCont.keyWord }}</section>
                <!--  -->
                <section class="article_cont" v-html="newsCont.htmlContext">{{ newsCont.htmlContext }}</section>
                <!--  -->
                <section class="article_hint">* </section>
                <!--  -->
                <section class="article_org_link">
                    <a :href="newsCont.url" target="_blank">{{ newsCont.webSite }} <span v-if="newsCont.url">()</span></a>
                </section>
                <!--  -->
                <section class="select_artc">
                    <a class="fl" @click="readPrev(newsCont.prevID)"><i class="icon_back"></i> </a>
                    <a class="fr" @click="readNext(newsCont.nextID)"> <i class="icon_next"></i></a>
                    <div class="clearfix"></div>
                </section>
            </div>
            <!--  -->
            <div id="operateBar">
                <ul class="operate_bar">
                    <li class="operate_item" @click="collect">                  
                        <div v-if="newsCont.isCollect">
                            <i class="operate_icon icon_collection is_collected"></i>
                            <span class="operate_title is_collected"></span>
                        </div>
                        <div v-else>
                            <i class="operate_icon icon_collection_line"></i>
                            <span class="operate_title"></span>
                        </div>
                    </li>
                </ul>
            </div>
            <!--  -->
            <ScrollModal :show="showModal">
                <!--  -->
                <button slot="h_left" class="button cancel_btn" @click="hideModal"></button>
                <div slot="h_center"></div>
                <button slot="h_right" class="button comfir_btn" @click="saveCollect"></button>
                <!--  -->
                <ul slot="content" class="group_list">
                    <li :class="['group_list_item', selectIndex == index ? 'active' : '']" v-for="(item, index) in groupList" :key="index" @click="getGroupSelect(item, index)">{{ item.name }}</li>
                </ul>
            </ScrollModal>
		</div>
	</div>
</template>

<script>
    // 
	import Loading from "components/Common/Loading.vue";
    import ScrollModal from "components/Modal/ScrollModal.vue"
    // Api
    import Api from "api/news.js";
    // 
    import Modal from "mixins/modal.js";
    // url
    import { GetCookie, GetUrlQuery } from "common/important.js";
    // js
    import Common from 'common/common.js'

	export default {
        name: "newsDetail",
        components: { Loading, ScrollModal },
        mixins: [ Modal ],
		data(){
			return{
                // 
                pageLoading: false,
                // Type
                pageType: null,
                // 
                newsId: 0,
                // 
                newsCont: {
                    title: '',
                    htmlContext: '',
                    keyWord: '',
                    publishDate: '',
                    url: null,
                    webSite: '',
                    prevID: 0,
                    nextID: 0,
                    isCollect: false
                },
                // 
                isDeleted: false,
                // 
                showModal: false,
                // 
                groupList: [],
                // 
                groupId: 0,
                // 
                selectIndex: -1
			}
		},
		created(){
            this.init();
        },
        methods:{
            init(){
                this.$store.commit('SET_NAV_TITLE', '');
                this.pageType = GetUrlQuery('type');
                this.newsId = GetUrlQuery('id');
                // type == 1 
                if(this.pageType == 1){
                    // 
                    this.$store.commit('SET_GOBACK_ROUTE', { name: 'ProjectNews', query: {} });
                }
                            
                this.getNewsCont(this.newsId);
                this.getGroups();                
            },
            // 
            getGroups(){
                Api.GetGroups()
				.then(res => {
					if(res.code == 200){
						this.groupList = res.data;
					}
					else this.showWarnModel(res.msg, 'warning');
				})
				.catch(err => console.log(err))
            },
            // 
            getNewsCont(newsId){
                // 
                this.newsId = newsId;
                // 
				this.pageLoading = true;

				Api.DeclareDetail(newsId)
				.then(res => {
					if(res.code == 200){
                        this.newsCont = res.data;                   
						// 
                        this.pageLoading = false;
                        
                        // 
						this.$nextTick(() => {
                            // img
							$('.article_cont').find('img').remove();
						})
                    }
					else {
                        // 
                        this.pageLoading = false;
                        this.newsCont.htmlContext = res.msg;
                        if(res.code == 0) this.isDeleted = true;
                    }
				})
				.catch(err => console.log(err))
            },
            // 
            readPrev(id){
                if(id !=0){
                    Common.GotoPage('NewsDetail', { id : id, type: this.pageType }, this);
                    this.getNewsCont(id);
                }
                else this.showWarnModel('', 'warning');
            },
            // 
            readNext(id){
                if(id !=0){
                    Common.GotoPage('NewsDetail', { id : id, type: this.pageType }, this);
                    this.getNewsCont(id);
                }
                else this.showWarnModel('', 'warning');
            },
            // 
            collect(){
                // 
                if(this.isCollected) return true;
                if(this.isDeleted) this.showWarnModel('!', 'warning');
                // 
                if(GetCookie('project_token')) {
                    this.showModal = true;
                }
			    else this.showWarnModel('', 'warning');
            },
            // 
            hideModal(){
                this.showModal = false;
                this.groupId = 0;
                this.selectIndex = -1;
            },
            // 
            getGroupSelect(item, index){
                this.groupId = item.id;
                this.selectIndex = index;
            },
            // 
            saveCollect(){
                Api.AddArticle({
                    declareId: this.newsId,
                    groupId: this.groupId
                })
				.then(res => {
					if(res.code == 200) {
                        this.getNewsCont(this.newsId);
                        this.hideModal();
                    }
					else this.showWarnModel(res.msg, 'warning');
				})
                .catch(err => console.log(err))
            }
        }
	};
</script>

<style lang="less" scoped>
    @import "../../assets/less/setting";
    @import "../../assets/less/article";
    @import "../../assets/less/operate_bar";

    .operate_item {
		width: 100%;
    }
    
    .is_collected{
        color: @base_color;
    }

    .group_list{
        margin-top: @navbar_h;
        width: 100%;
    }

    .group_list_item{
        display: block;
        height: 46*@rem;
        line-height: 46*@rem;
		padding: 0 12*@rem;
		border-bottom: @border_light;
		text-align: center;
		cursor: pointer;

		&:hover{
			color: @base_color;
			background: @base_hover_color;
        }
        
        &.active{
            color: #fff;
			background: @base_color;
        }
    }
</style>



