<template>
	<div id="userCenter">
		<!--  -->
		<Loading v-if="pageLoading"></Loading>
		<!--  -->
		<div v-else>
			<!--  -->
			<form class="cont_frame">
				<div class="form_line">
					<input type="password" v-model="form.oldPassword" placeholder=""/>
				</div>
				<div class="form_line">
					<input type="password" v-model="form.newPassword" placeholder=""/>
				</div>
				<!-- <div class="form_line">
					<input type="password" v-model="form.comfirmPassword" placeholder=""/>
				</div> -->
				<div class="form_line">
					<input type="button" class="button" value="" @click="validForm"/>
				</div>
			</form>
		</div>
	</div>
</template>

<script>
	// 
	import Loading from "components/Common/Loading.vue";
	// JS
	import Common from 'common/common.js';
	import { GetCookie } from 'common/important.js';
	// Api
	import Api from "api/user_center.js";
	//Vuex
	import { mapGetters } from 'vuex';
	// 
	import Modal from "mixins/modal.js"

	export default {
		name: "userCenter",
		mixins: [ Modal ],
		components: { Loading },
		computed: {
            ...mapGetters([ 'userAccount' ]),
        },
		data(){
			return{
				// 
				pageLoading: false,
				// 
				form:{
					// 
					oldPassword: '',
					// 
					newPassword: '',
					// // 
					// comfirmPassword: ''
				}
			}
		},
		created(){
			this.init();
		},
		methods:{
			// 
			init(){
                this.$store.commit('SET_NAV_TITLE', '');
			},
			// 
			validForm(){
				if(this.form.oldPassword == ''){
					this.showWarnModel('', 'warning');
					return false;
				}
				else if(this.form.newPassword == ''){
					this.showWarnModel('', 'warning');
					return false;
				}
				// else if(this.form.newPassword == ''){
				// 	this.showWarnModel('', 'warning');
				// 	return false;
				// }
				// else if(this.form.newPassword != this.form.comfirmPassword){
				// 	this.showWarnModel('2', 'error');
				// 	return false;
				// }
				else{
					this.onSubmit();
				}
			},
			// 
			onSubmit(){
				Api.ChangePassword(this.form)
				.then(res => {
					if(res.code == 200){
						this.showWarnModel('', 'success');
						Common.GotoPage('UserCenter', {} , this);
					}
					else this.showWarnModel(res.msg, 'warning');
				})
				.catch(err => console.log(err))
			}
		}
	};
</script>

<style lang="less" scoped>
	// 
	@import "../../assets/less/setting";
</style>



